import React from 'react';
import logo from './logo.svg';
import './App.css';
import Todo from './component/Todo';

//axios used for fetching
import axios from 'axios';
//howler library for audio 
import {Howl, Howler} from 'howler'
import bruh from './audioclips/1.mp3'

const audioclips = [
  //object inside array 
  {sound: bruh, label:'Play lofi music'}
]

class App extends React.Component {
  state = {
    advice: '',
  }

  componentDidMount() {
    this.fetchAdvice();
  }

  fetchAdvice = () => {
    //everytime we reload this API it returns a new set of json data 
    //{"slip": { "id": 3, "advice": "Don't eat non-snow-coloured snow."}}
    axios.get('https://api.adviceslip.com/advice')
      .then((response) => {
        console.log('response', response)
        //destructuring the advice 
        const { advice } = response.data.slip;
        //the slip contains id and advice that we want to extract 
        //we update the state 'advice' with the new data inside the response object

        this.setState({ advice });
        //updating state property using setState, so we can access the updated state in render 
      })
      .catch((error) => {
        console.log(error);
      });
  }

  //for the audio 
  SoundPlay = (src) => {
    const sound = new Howl({
      src
    })
    sound.play()
  }

  RenderButtonSound = ()=> {
    return audioclips.map((soundobj, index) => {
      return (
        <button className='btnnew' key={index} onClick={() => this.SoundPlay(soundobj.sound)}>
        {soundobj.label}
        </button>
    )
    })
  }

  render() {
    Howler.volume(1.0)

    return (
      <div className="app">
        <div className="card">
          <h1 className="heading">{this.state.advice}</h1>
          <button className="btnnew" onClick={this.fetchAdvice}>
                        <span>GIVE ME ADVICE!</span>
                        {/* when we call the fetchAdvice function it updates the advice in the state 'advice' and returns it in the heading where were calling to display it */}
          </button>
          {this.RenderButtonSound()}
        </div>
        <Todo />
      </div>
    );
  }
}


export default App;
